.signup{
  display: flex;
  min-height: 100vh;
  max-height: 100vh;
  .graphic{
    width: 25vw;
    background: hsla(354, 82%, 73%, 1);
    padding: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    overflow-y: hidden;

    .title{
      text-align: center;
      color: hsla(354, 54%, 42%, 1);
    }
    .info{
      .img{
        text-align: center;
        img{
          width: 60%;
          max-width: 60%;
        }
      }
      .txt{
        text-align: center;
        color:hsla(354, 76%, 30%, 1);
      }
    }
  }
  .form{
    flex: 1;
    padding: 25px 50px;
    overflow-x: hidden;
    overflow-y: auto;

    fieldset{
      border: none;
      color:rgba(0,0,0,0.3);
      width: 40vw;
      margin: auto;
      margin-top: 50px;
      legend{
        padding: 0 10px;
      }
    }

    .signin_prompt{
      font-size: .8em;
      display: flex;
      justify-content: flex-end;
      margin-bottom: 25px;
      a{
        color: inherit;
        margin-left: 5px;
      }
    }
    .title{
      font-size: 1.5em;
      width: 40vw;
      margin: auto;
      margin-bottom: 25px;
    }
    .field{
      width: 40vw;
      margin: auto;
      margin-bottom: 25px;
      display: flex;
      flex-direction: column;
      .radio_group{
        display: flex;
        .radio{
          margin-right: 15px;
        }
      }

      .field_label{
        margin-bottom: 10px;
      }

      input[type=text]:focus,
      input[type=tel]:focus,
      input[type=password]:focus,
      select:focus{
        border-color: #DE1E30;
      }

      input[type=text],
      input[type=tel],
      input[type=password],
      select{
        padding: 8px 10px;
        border-radius: 8px;
        border: 1px solid rgba(0,0,0,0.3);
        outline: none;
      }
    }
    .field.signup_type{
      display: flex;
      justify-content: space-between;
      flex-direction: row;
    }

    .PhoneInput{
      width: 100%;
    }

    .consent{
      font-size: .8em;
    }

    .submit{
      width: 20vw;
      margin: auto;
      input[type=submit]{
        background: none;
        border: none;
        cursor: pointer;
        width: 100%;
        background-color: rgba(47,156,240,1);
        color: rgba(229,251,254,1);
        border-radius: 8px;
        padding: 10px 15px;
        outline: none;
      }
    }
  }
}